import stocks from '../../data/stocks.js'

const state = {
    stocks: []
};

const mutations = {
    'SET_STOCKS'(state, stocks) {
        state.stocks = stocks;
    },

    'RANDOMIZE_STOCKS'(state) {
        state.stocks.forEach(stock => {
            stock.change = Math.round((Math.random() * 0.5 + 0.75) * 100) / 100;
            stock.price = Math.round(stock.price * stock.change);
        });
    }
};

const actions = {
    initStocks: ({ commit }) => {
        commit('SET_STOCKS', stocks);
    },

    randomizeStocks: ({ commit }) => {
        commit('RANDOMIZE_STOCKS');
    }
};

const getters = {
    stocks: state => {
        return state.stocks;
    }
};

export default {
    state,
    mutations,
    actions,
    getters
}


