<template>
    <div class="row">
        <app-stock v-show="stocks.length" v-for="stock in stocks" :stock="stock" :key="stock.name"></app-stock>
        <h4 v-show="!stocks.length" class="w-100 mx-2 text-center">Portfolio is empty.<br>Click "Stocks" button and buy something!</h4>
    </div>
</template>


<script>

    import Stock from './Stock.vue'

    export default {
        computed: {
            stocks() {
                return this.$store.getters.portfolioStocks;
            }
        },

        components: {
            appStock: Stock
        }
    }

</script>


