const state = {
    funds: 10000,
    stocks: []
};

const mutations = {
    'BUY_STOCK'(state, { stockId, quantity, stockPrice }) {
        const record = findStock(stockId, state.stocks);
        if (record) {
            record.quantity += quantity;
        } else {
            state.stocks.push({
                id: stockId,
                quantity
            });
        }
        state.funds -= stockPrice * quantity;
    },

    'SELL_STOCK'(state, { stockId, quantity, stockPrice }) {
        const record = findStock(stockId, state.stocks);
        if (record.quantity > quantity) {
            record.quantity -= quantity;
        } else {
            state.stocks.splice(state.stocks.indexOf(record), 1);
        }
        state.funds += stockPrice * quantity;
    },

    'SET_PORTFOLIO'(state, { funds, portfolioStocks: stocks }) {
        state.funds = funds;
        state.stocks = stocks;
    }
};

const actions = {
    sellStock({ commit }, order){
        commit('SELL_STOCK', order);
    },

    buyStock: ({ commit }, order) => {
        commit('BUY_STOCK', order);
    },
};

const getters = {
    portfolioStocks(state, getters) {
        return state.stocks.map(stock => {
            const record = findStock(stock.id, getters.stocks);
            return {
                id: stock.id,
                quantity: stock.quantity,
                name: record.name,
                price: record.price,
                change: record.change
            }
        });
    },

    funds(state) {
        return state.funds;
    }
};

export default {
     state,
     mutations,
     actions,
     getters
}

function findStock(stockId, stocks) {
    return stocks.find(element => element.id === stockId);
}


