<template>
    <div class="col-12 col-md-6 col-lg-4 mt-3 my-md-3">
        <div class="card">
            <h5 class="card-header bg-light d-flex align-items-center justify-content-between">
                <span>{{ stock.name }}</span>
                <small class="text-info ml-3"> {{ stock.quantity }} 
                    <span class="text-dark">/</span> 
                    <span :class="{ 'text-success': stockRises, 'text-danger': !stockRises }">
                        {{ stock.price | currency }}
                        <span>({{stock.change | percentage}}){{ stockRises ? '&#9650;' : '&#9660;' }}</span>
                    </span>
                </small>
            </h5>
            <div class="card-body">
                <div class="justify-content-between d-flex">
                    <input min="1" :max="stock.quantity" type="number" class="form-control"
                     :class="{ danger: checkSellQuantity && sellQuantity !== '' }"
                     v-model="sellQuantity" placeholder="Quantity">

                    <button class="btn btn-danger ml-2"
                     @click="sellStock"
                     :disabled="checkSellQuantity">Sell</button>
                </div>
                <small class="text-muted">{{ stock.quantity }} available</small>
            </div>
        </div>
    </div>
</template>


<script>
    export default {
        props: ['stock'],

        data() {
            return {
                sellQuantity: ''
            };
        },

        methods: {
            sellStock() {
                const order = {
                    stockId: this.stock.id,
                    stockPrice: this.stock.price,
                    quantity: Number(this.sellQuantity)
                };

                this.$store.dispatch('sellStock', order);
                this.sellQuantity = '';
            }
        },

        computed: {
            checkSellQuantity() {
                return !this.sellQuantity || this.sellQuantity.match(/[^0-9]/)
                                                || this.sellQuantity > this.stock.quantity;
            },

            stockRises() {
                return this.stock.change >= 1 || this.stock.change === 0;
            }
        }
    }
</script>



