import Vue from 'vue'

export const loadData = ({ commit }) => {
    Vue.http.get('data.json')
        .then(response => response.json())
        .then(data => {
            if (data) {
                const { stocks, funds, portfolioStocks = [] } = data;
                commit('SET_PORTFOLIO', { portfolioStocks, funds });
                commit('SET_STOCKS', stocks);  
            }
        });
};


