<template>
    <div>
        <div class="text-center">
            <h1>Stock Trader app</h1>
            <h2>Trade or view your Portfolio</h2>
        </div>
        <hr>
        <div>
            <h4 class="font-weight-bold">How to use it</h4>
            <div class="ml-3">
                <h5>To save and load your result click appropriate buttons in a menu.</h5>
                <h5>To change stocks' currencies click the 'End day' button.</h5>
                <h5>To return the initial state of app just reload the page. </h5>
            </div>
        </div>
        <hr>
        <p class="text-center">Your Funds: {{ funds | currency }}</p>
    </div>
</template>


<script>
    export default {
        computed: {
            funds() {
                return this.$store.getters.funds;
            }
        }
    }
</script>




