import Vue from 'vue'
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'
import App from './App.vue'
import { routes } from './routes'
import store from './store/store'

Vue.use(VueRouter);
Vue.use(VueResource);

Vue.http.options.root = 'https://stock-trader-56f76.firebaseio.com/';

Vue.filter("currency", value => '$' + value.toLocaleString());
Vue.filter("percentage", value => value >= 1 || value === 0 ? `+${Math.abs(Math.round(value * 100) - 100) % 100}%` : 
														      `-${Math.abs(Math.round(value * 100) - 100) % 100}%`);

const router = new VueRouter({
	routes,
	mode: 'history'
})

new Vue({
	el: '#app',
	router,
	store,
	render: h => h(App)
})



