<template>
  	<nav class="navbar navbar-expand-lg navbar-dark bg-dark mb-5">
		<router-link to="/stocks-trader/home" class="navbar-brand">Stock Trader</router-link>

    	<button class="navbar-toggler" type="button" data-toggle="collapse"
    	 data-target="#navbarNavDropdown" aria-controls="navbarNavDropdown"
    	 aria-expanded="false" aria-label="Toggle navigation" @click="show=!show">
    		<span class="navbar-toggler-icon"></span>
    	</button>

    	<div class="collapse navbar-collapse" :class="{ show }" id="navbarNavDropdown">
    		<ul class="navbar-nav">
    			<router-link to="/stocks-trader/portfolio" tag="li" class="nav-item" activeClass="active">
    				<a class="nav-link">Portfolio</a>
    			</router-link>
    			<router-link to="/stocks-trader/stocks" tag="li" class="nav-item" activeClass="active">
    				<a class="nav-link">Stocks</a>
    			</router-link>
    		</ul>

    		<ul class="navbar-nav ml-auto">
    			<li>
    				<a class="nav-link" href="#" @click="endDay">End day</a>
    			</li>
    			<li class="nav-item dropdown"
				 :class="{ show: dropdownShow }"
				 @click="dropdownShow = !dropdownShow">
    		    	<a class="nav-link dropdown-toggle" href="#" data-toggle="dropdown">
    		    		Save & Load
    		    	</a>

    		    	<div class="dropdown-menu"
					 :class="{ show: dropdownShow }">
    		    		<a class="dropdown-item" href="#" @click="saveData">Save result</a>
    		    		<a class="dropdown-item" href="#" @click="loadData">Load result</a>
    		    	</div>
    			</li>
    		</ul>	

    		<span class="navbar-text text-warning font-weight-bold pl-lg-2">
    			Funds: {{ funds | currency }}
			</span>
		</div>
	</nav>
</template>

<script>

	import { mapActions } from 'vuex';

	export default {
		data() {
			return {
				show: false,
				dropdownShow: false
			}
		},

		computed: {
			funds() {
				return this.$store.getters.funds;
			}
		},

		methods: {
			...mapActions([
				'randomizeStocks', 'loadData'
			]),

			endDay() {
				this.randomizeStocks();
			},

			saveData() {
				const data = {
					funds: this.$store.getters.funds,
					portfolioStocks: this.$store.getters.portfolioStocks,
					stocks: this.$store.getters.stocks
				};

				this.$http.put('data.json', data);
			}
		}
	}
	  
</script>

<style scoped>
    .navbar-brand {
        font-size: 1.5rem !important;
    }
</style>



