<template>
    <div class="col-12 col-md-6 col-lg-4 mt-3 my-md-3">
        <div class="card border-0 text-light">
            <h5 class="card-header bg-dark d-flex justify-content-between">
                <span>{{ stock.name }}</span>
                <span :class="{ 'text-success': stockRises, 'text-danger': !stockRises }">
                    {{ stock.price | currency }}
                    <span>({{stock.change | percentage}}){{ stockRises ? '&#9650;' : '&#9660;' }}</span>
                </span>
            </h5>
    
            <div class="card-body border border-top-0 justify-content-between d-flex">
                <input min="1" type="number" class="form-control"
                 :class="{ danger: checkBuyQuantity && buyQuantity !== '' }"
                 v-model="buyQuantity" placeholder="Quantity">
 
                <button class="btn btn-success ml-2"
                 @click="buyStock"
                 :disabled="checkBuyQuantity">Buy</button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        props: ['stock'],

        data() {
            return {
                buyQuantity: ''
            };
        },

        methods: {
            buyStock() {
                const order = {
                    stockId: this.stock.id,
                    stockPrice: this.stock.price,
                    quantity: Number(this.buyQuantity)
                };

                this.$store.dispatch('buyStock', order);
                this.buyQuantity = '';
            }
        },

        computed: {
            funds() {
                return this.$store.getters.funds;
            },

            checkBuyQuantity() {
                return !this.buyQuantity || this.buyQuantity.match(/[^0-9]/)
                                                || this.buyQuantity * this.stock.price > this.funds;
            },

            stockRises() {
                return this.stock.change >= 1 || this.stock.change === 0;
            }
        }
    }
</script>

<style>
    .card-body input {
        min-width: 0;
        max-width: 14rem !important;
    }

    button,
    button:focus {
        box-shadow: none !important;
        outline: none !important;
    }

    .card {
        word-break: break-all;
    }

    input.danger,
    input.danger:focus {
        border-color: #ff80bd;
        box-shadow: 0 0 0 .2rem rgba(255, 0, 0, .35);
    }
</style>


